<template>
  <article>
    <img v-bind:src="this.logo" alt="logo_uploader" />
    <em>Uploader {{uploader.username}}</em>
  </article>
</template>

<script>
export default {
  props: ["uploader"],
  name: "UploaderItem",
  computed: {
    unreadFiles: function () {
      let numberUnreadFiles = 0;
      if (this.uploader.files !== undefined) {
        this.uploader.files.forEach((element) => {
          if (element.seen === "") numberUnreadFiles++;
        });
        return numberUnreadFiles;
      } else {
        return 0;
      }
    },
    logo: function () {
      if (this.uploader.logo === "") {
        return "/default_profile_picture.png";
      } else {
        return `data:image/jpeg;base64, ${this.uploader.logo}`;
      }
    },
  },
};
</script>

<style scoped>
article {
  width: 95%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  height: 15vh;
}

article img {
  height: 14vh;
  width: 14vh;
  border: 3px solid black;
  border-radius: 7vh;
  margin-right: 2vw;
}

article em {
  flex-basis: 60%;
  display: inline-block;
  font-size: 8vh;
  font-style: normal;
  font-family: "Mitr";
}

</style>