<template>
  <section>
    <div>
      <input type="text" v-model="hashtag" placeholder="filter by hashtag..." />
    </div>
    <table>
      <thead>
        <tr>
          <th>Name</th>
          <th>Hashtags</th>
          <th>Uploaded</th>
          <th>Seen</th>
          <th>Download</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(file, index) in filesFilter" v-bind:key="index">
          <td>{{file.name}}</td>
          <td>{{file.hashtags.join(", ")}}</td>
          <td>{{file.uploaded}}</td>
          <td v-if="file.seen!==''">{{file.seen}}</td>
          <td v-else>unseen file</td>
          <td>
            <a v-bind:href="file.link">
              <img src="download_icon.png" alt="download" />
            </a>
          </td>
        </tr>
      </tbody>
    </table>
  </section>
</template>

<script>
export default {
  props: ["files"],
  data: function () {
    return {
      hashtag: "",
    };
  },
  computed: {
    filesFilter: function () {
      if (this.hashtag !== "") {
        return this.files.filter((element) =>
          element.hashtags.includes(this.hashtag)
        );
      } else {
        return this.files;
      }
    },
  },
};
</script>

<style scoped>
section div {
  display: flex;
  width: 95.5%;
  margin-right: auto;
  margin-left: auto;
  flex-direction: row-reverse;
}

section div input[type="text"] {
  margin-right: 0.4vw;
  height: 4vh;
}

table {
  width: 95%;
  margin-top: 0.5vh;
  margin-right: auto;
  margin-left: auto;
  height: 60vh;
  display: flex;
  flex-flow: column nowrap;
  border: 3px solid black;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

table thead {
  flex-basis: 14%;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  background-color: rgba(0, 0, 0, 0.83);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

table thead tr {
  flex-basis: 100%;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

table thead tr th {
  flex-basis: 25%;
  color: rgb(214, 139, 0);
}

table tbody {
  flex-basis: 86%;
  display: flex;
  flex-flow: column nowrap;
  max-height: 50vh;
  overflow-y: auto;
}

table tbody tr {
  flex-basis: 12%;
  flex-shrink: 0;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

table tbody tr:nth-child(odd) {
  background-color: rgb(230, 230, 230);
}

table tbody tr td {
  flex-basis: 25%;
  text-align: center;
}

table tbody tr td a {
  display: flex;
  flex-direction: row nowrap;
  justify-content: center;
  align-items: center;
  height: 5vh;
}

img {
  height: 100%;
}

::-webkit-scrollbar {
  display: none;
}
</style>